import * as React from 'react';
import Context from '../context';

interface ProcessoProps {
    procObj: {
        'Assunto COMPROT'?: string;
        'CPF Responsvel Atual'?: string;
        'CPF Responsvel ltimo'?: string;
        'CPF/CNPJ Corresponsvel'?: string;
        'CPF/CNPJ Devedor Principal'?: string;
        'Cdigo da Equipe Atual'?: string;
        'Cdigo de Receita na Dvida Ativa'?: string;
        'DEBCAD'?: string;
        'Data Distribuio ltima'?: string;
        'Data Entrada Atividade'?: string;
        'Data Entrada Unidade'?: string;
        'Data Situao da Inscrio'?: string;
        'Data da Atualizao do Valor Atualizado da Inscrio'?: string;
        'Data da Inscrio'?: string;
        'Data do Protocolo'?: string;
        'Idade Contribuinte'?: string;
        'Indicador Grande Devedor'?: string;
        'Indicadores'?: string;
        'Informaes'?: string;
        'NI Contribuinte'?: string;
        'Nome Atividade Atual'?: string;
        'Nome Atividade ltima'?: string;
        'Nome Contribuinte'?: string;
        'Nome Equipe Atual'?: string;
        'Nome Equipe ltima'?: string;
        'Nome Responsvel'?: string;
        'Nome Tarefa Atual'?: string;
        'Nome Unidade Atual'?: string;
        'Nome Unidade ltima'?: string;
        'Nome ltimo Documento Confirmado'?: string;
        'Nmero Processo'?: string;
        'Nmero da Inscrio Derivada'?: string;
        'Nmero de Inscrio'?: string;
        'Nmero do Processo Judicial'?: string;
        'Nmero do Processo Judicial da Inscrio'?: string;
        'Nmero do Requerimento (SICAR-PGFN)'?: string;
        'Porte Contribuinte'?: string;
        'Processos Vinculados'?: string;
        'Procuradoria Responsvel'?: string;
        'Procuradoria de Inscrio'?: string;
        'Quantidade Volumes'?: string;
        'Saldo na Situao Atual (Multa de Ofcio)'?: string;
        'Saldo na Situao Atual (Principal)'?: string;
        'Situao'?: string;
        'Situao da Inscrio'?: string;
        'Tipo Processo'?: string;
        'Todos'?: string;
        'Tributo'?: string;
        'Valor Atualizado da Inscrio'?: string;
        'Valor Originrio Lanado/Pleiteado (Principal)'?: string;
        'Valor Processo'?: string;
        'Valor Total das Inscries'?: string;
        'Valor do Crdito Consolidado'?: string;
        'Valor do Crdito Lanado (Multa de Ofcio)'?: string;
    };
    processo: string;
}

const stringToNumber = (s: string) => {
    let arr = Array.from(s);
    const num = arr.reduce((pr, cu, i) => {return (cu.charCodeAt(0) + pr); } , 0);
    return num % 360;
};

const getColor = (situacao: string) => {
    let num = stringToNumber(situacao);    
    let color = `hsla(${num}, 100%, 96%, 0.9)`;
    return color;
};

const Processo: React.SFC<ProcessoProps> = (props) => {
    const { processo, procObj } = props;
    const ultres = procObj['Nome Responsvel'];
    const tarefa = procObj['Nome Tarefa Atual'];
    const dataEntrada = procObj['Data Entrada Atividade'];
    let numeroBelo = processo;
    if (procObj && procObj['Nmero Processo']) {
        numeroBelo = (procObj['Nmero Processo'] as string).replace('D', '').trim();
    }
    return (
        <Context.Consumer>
        {({
            selecionado, 
            situacao,
            loadPDF,
            setState, 
            focaNaDivPrincipal
        }) => {
            return (
            <span 
                className="processo-div-component"   
                
            >
                
                <span
                    className="span-processo-title"                    
                    style={selecionado === processo ? {color: 'rgb(0, 125, 89)'} : {}}
                    onClick={() => {
                        loadPDF(processo);
                        setState({selecionado: processo}, focaNaDivPrincipal);
                    }}
                >{numeroBelo}
                </span>
                    {situacao[processo] && 
                    <div 
                        className="div-processo-caracteristicas div-wrap-flex"
                        style={{
                            backgroundColor: getColor(situacao[processo]),
                            border: '2px solid rgb(178, 189, 239)',
                            borderRadius: '12px',
                            fontSize: '0.6em',
                        }}
                    >
                        {situacao[processo]}
                    </div>}
                    {ultres && <div className="div-processo-caracteristicas div-wrap-flex">
                        {ultres.split(' ')[0]}
                    </div>}
                    {tarefa && <div className="div-processo-caracteristicas div-wrap-flex">
                        {tarefa}
                    </div>}
                    {dataEntrada && 
                        <div 
                            className="div-processo-caracteristicas div-wrap-flex"
                            style={{fontSize: '0.85em'}}
                        >
                        {dataEntrada}
                        </div>
                         }

            </span>
        );
        }}
        </Context.Consumer>
    );
};
export default Processo;


